// when ready, function messages the taskpane to send the item categories, and it receives and displays them
(function() {
    "use strict";
    Office.onReady()
    .then(function() {
        Office.context.ui.addHandlerAsync(Office.EventType.DialogParentMessageReceived, displayItemCategories, registered);
    });

    // function displays the item categories
    function displayItemCategories(arg) {
        let itemCategoryList = JSON.parse(arg.message);
        let itemCategoryListElem = document.getElementById("itemCategoryList");
        itemCategoryListElem.innerText = itemCategoryList.active;
    }

    // function sends a message to the taskpane when ready
    function registered(asyncResult) {
        if (asyncResult.status === Office.AsyncResultStatus.Succeeded) Office.context.ui.messageParent("connected");
        else Office.context.ui.messageParent("failed");
    }
}());